.modal {
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
    z-index: 99999;
    position:fixed;
    background: white;
    width: 80%;
    border: 1px solid black;
    height: auto;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    border-radius: 15px;
    padding: 1em;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
